import axios from 'axios';
import store from '../store'
import apiToken from '../scripts/ApiToken.js';
import ssoServer from './ssoServer.js'

axios.defaults.timeout = 5000;

axios.interceptors.request.use(
    config => {

        if (store.state.user.account) {
            config.headers.Authorization = store.state.user.account;
        } else {
            let token = apiToken.getCookie('auxsso-token');
            config.headers.Authorization = token;
        }

        console.log(config);

        return config;
    },
    error => {
        return Promise.reject(error);
    }
);

axios.interceptors.response.use(
    response => {
        return response;
    },
    error => {

        if (error.response) {

            if (error.response.status == 401) {
                store.commit('logout');
                console.log('=========401错误，注销后，跳转SSO==========')
                window.location = ssoServer.server;
            }
        }

        return Promise.reject(error);
    }
);

export default axios;


