const state = {
  token:'no-token',
  oauth : false
}

const mutations = {

  oauthUser(state , token) {
    state.token = token;
    state.oauth = true;
  },

  logout(state){
    state.token = 'no-token';
    state.oauth = false;
  }

}

export default {
  state,
  mutations
}


